.veEditor {
  position: relative;
}
.veVectorInteractionWrapper:focus {
  -webkit-box-shadow: 0 4px 2px -2px #9ecaed;
          box-shadow: 0 4px 2px -2px #9ecaed;
}

.taProductViewer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.RowViewSide,
.CircularViewSide {
  width: 100%;
}
.tg-editor-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ve-clickable {
  cursor: pointer;
  opacity: 0.7;
}
.ve-clickable:hover {
  opacity: 1;
}
.ve-clickable-black {
  cursor: pointer;
  color: rgb(78, 78, 78);
}
.ve-clickable-black:hover {
  color: black;
}
.ve-close-panel-button {
  z-index: 400;
  color: grey;
  height: 25px;
  width: 35px;
}

.reflex-layout > .reflex-element {
  overflow-y: hidden !important;
}
.reflex-layout.reflex-container {
  height: inherit !important;
}

.preview-mode-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.preview-mode-view-fullscreen {
  width: 200px;
  height: 40px;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  position: absolute;
  background: #2b95d6;
  color: white;
  display: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
}

.preview-mode-container:hover .preview-mode-view-fullscreen {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.preview-mode-view-fullscreen:hover {
  background: #0e5a8a;
}
