.menu-bar {
  background: white;
  color: inherit;
  text-decoration: none;
  z-index: 19;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 34px;
  width: 100%;
  padding-left: 15px;
}

.menu-bar-item {
  margin: 2px;
}

.menu-bar .pt-popover {
  margin-top: -1px !important;
}

.menu-bar-popover .pt-popover .pt-popover-arrow {
  display: none;
}

.menu-bar-popover .pt-popover-wrapper {
  width: 100%;
}
