.veStatusBar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  max-height: 25px;
  min-height: 25px;
  /* background: #f3f3f3; */
  overflow-x: auto;
  border-top: lightgrey 0.1px solid;
  border-bottom: lightgrey 0.1px solid;
}
.veStatusBar::-webkit-scrollbar {
  display: none;
}

.veStatusBarItem {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  margin-left: auto;
  margin-right: auto;
}
.veStatusBarSpacer {
  height: 100%;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  width: 1px;
  min-width: 1px;
  background: lightgrey;
  margin-left: 3px;
  margin-right: 3px;
}
